import { useCallback, useEffect, useState } from 'react'
import BigNumber from 'bignumber.js'

import { getContractBalance, getBalanceNumber, getSushiContract, getColaPrice, getSodaMasterAddress, getSodaPrice } from '../sushi/utils' // 
//import { getSodaSupply, getSodaContract } from '../sushi/utils'

// hooks
import useSushi from './useSushi'
import useBlock from './useBlock'
import { useActiveWeb3React } from '.'

const useChecking = () => {
  const [totalApy, setApy] = useState(0)
  const { account } = useActiveWeb3React()
  const sushi = useSushi()  
  const block = useBlock()
  const colaContract = getSushiContract(sushi)
  const sodaMasterAddress = getSodaMasterAddress(sushi)

  const fetchBalance = useCallback(async () => {
	
    const lpamout = await getContractBalance(colaContract, sodaMasterAddress)
	const colaPrice = await getColaPrice(sushi)	
	const sodaPrice = await getSodaPrice(sushi)
	const totalStaked = getBalanceNumber(new BigNumber(lpamout)) * colaPrice //All cola in SodaMaSter to usd
	const blockPerYear = 10512000
	const sodaPerBlock = 0.0026 //0.0026
	
	const totalApy = ((sodaPrice * blockPerYear * sodaPerBlock) / totalStaked)*100
	
    setApy(totalApy)
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [account, sushi])

  useEffect(() => {
    if (account && sushi) {
      fetchBalance()
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [account, setApy, block, sushi])

  return totalApy
}

export default useChecking
