import { useCallback, useEffect, useState } from 'react'
import BigNumber from 'bignumber.js'

import { getContractBalance, getBalanceNumber, getColaPrice, getBNBPrice, getWethContract, getPoolTotalAllocPoint, getPoolAllocPoint, getMasterChefContract, getBUSDUSDTPair, getWbusdContract, getSodaAddress, getSodaPrice, getSodaContract, getMasterChefAddress, getCOLASODAPair, getSushiContract } from '../sushi/utils' // 
//import { getSodaSupply, getSodaContract } from '../sushi/utils'

// hooks
import useSushi from './useSushi'
import useBlock from './useBlock'
import { useActiveWeb3React } from '.'

const useChecking = (lpTokenAddress: string, pid: number) => {
  const [totalApy, setApy] = useState(0)
  const { account } = useActiveWeb3React()
  const sushi = useSushi()  
  const block = useBlock()
  const Contract = getWethContract(sushi)
  const MasterChefContract = getMasterChefContract(sushi)
  const wbusdContract = getWbusdContract(sushi)
  const BUSDUSDTPair = getBUSDUSDTPair(sushi)
  const sodaAddress = getSodaAddress(sushi)
  const sodaContract = getSodaContract(sushi)
  const colaMasterAddress = getMasterChefAddress(sushi)
  const COLASODAPair = getCOLASODAPair(sushi)
  const colaContract = getSushiContract(sushi)

  const fetchBalance = useCallback(async () => {	
    
	const allowPoint = await getPoolAllocPoint(MasterChefContract, pid)
	const totalPoint = await getPoolTotalAllocPoint(MasterChefContract)
	const NumberPoolWeight = allowPoint / totalPoint
	
	const colaPrice = await getColaPrice(sushi)
	const bnbprice = await getBNBPrice(sushi)
	const sodaPrice = await getSodaPrice(sushi)
	
	const blockPerYear = 10512000
	const colaPerBlock = 0.14 //cola per year (earlt maker 1532160) //0.14
	
	if(lpTokenAddress !== BUSDUSDTPair){
		if(lpTokenAddress === sodaAddress){ //soda
			const lpamout = await getContractBalance(sodaContract, colaMasterAddress)	
			const totalStaked = getBalanceNumber(new BigNumber(lpamout)) * sodaPrice
			const totalApy = ((colaPrice * blockPerYear * colaPerBlock * NumberPoolWeight) / totalStaked)*100	
			setApy(totalApy)
		}
		else if(lpTokenAddress === COLASODAPair){
			const sodaamout = await getContractBalance(sodaContract, lpTokenAddress)	//soda
			const colaamout = await getContractBalance(colaContract, lpTokenAddress)	//cola			
			const totalSodaStaked = getBalanceNumber(new BigNumber(sodaamout)) * sodaPrice
			const totalColaStaked = getBalanceNumber(new BigNumber(colaamout)) * colaPrice
			
			const totalStaked = totalSodaStaked + totalColaStaked
			
			const totalApy = ((colaPrice * blockPerYear * colaPerBlock * NumberPoolWeight) / totalStaked)*100	
			setApy(totalApy)
		}
		else{ //normal		
			const lpamout = await getContractBalance(Contract, lpTokenAddress)	
			const totalStaked = getBalanceNumber(new BigNumber(lpamout)) * bnbprice * 2 //bnb in pool		
			const totalApy = ((colaPrice * blockPerYear * colaPerBlock * NumberPoolWeight) / totalStaked)*100	
			setApy(totalApy)
		}		
	}
	else{
		const lpamout = await getContractBalance(wbusdContract, lpTokenAddress)
		const totalStaked = getBalanceNumber(new BigNumber(lpamout)) * 2 //busd in pool		
		const totalApy = ((colaPrice * blockPerYear * colaPerBlock * NumberPoolWeight) / totalStaked)*100	
		setApy(totalApy)
	}
	
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [account, sushi])

  useEffect(() => {
    if (account && sushi) {
      fetchBalance()
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [account, setApy, block, sushi])

  return totalApy
}

export default useChecking
