import BigNumber from 'bignumber.js'
import React, { useEffect, useState } from 'react'
import styled from 'styled-components'
import useSushi from '../../../hooks/useSushi'
import { getColaBurn, getSodaBurn, getColaPrice, getSodaPrice} from '../../../sushi/utils'
import { getBalanceNumber, currencyFormat } from '../../../utils/formatBalance'

const Balances: React.FC = () => {	

  const [totalColaBurn, setTotalColaBurn] = useState<BigNumber>()
  const [totalSodaBurn, setTotalSodaBurn] = useState<BigNumber>()
  const [colaBurnInUSD, setColaBurnInUSD] = useState<number>()
  const [sodaBurnInUSD, setSodaBurnInUSD] = useState<number>()
  const sushi = useSushi()

  useEffect(() => {
    async function fetchTotalSupply() {
		
      const colaBurn = await getColaBurn(sushi)
	  const sodaBurn = await getSodaBurn(sushi)	  
	  
	  const ColaPrice = await getColaPrice(sushi)
	  const SodaPrice = await getSodaPrice(sushi)
	  
	  const colaBurnInUSD = await getBalanceNumber(colaBurn)*ColaPrice
	  const sodaBurnInUSD = await getBalanceNumber(sodaBurn)*SodaPrice
	  
      setTotalColaBurn(colaBurn)
	  setTotalSodaBurn(sodaBurn);
	  setColaBurnInUSD(colaBurnInUSD)
	  setSodaBurnInUSD(sodaBurnInUSD)
    }
    if (sushi) {
      fetchTotalSupply()
    }
  }, [sushi, setTotalColaBurn, setTotalSodaBurn, colaBurnInUSD, setSodaBurnInUSD])

  return (
		<StyledWrapper>
			<StyledCard>
				<StyledName>
					🔥 Total YAKUZA burned since launch
				</StyledName>
				<StyledTotalBurn>
					<BurnAmount>{totalColaBurn ? currencyFormat(getBalanceNumber(totalColaBurn), 3) : 'Locked'}</BurnAmount>
					{colaBurnInUSD ? `$${currencyFormat(colaBurnInUSD, 0)}` : ''}
				</StyledTotalBurn>
			</StyledCard>
			<StyledCard>
				<StyledName>
					🔥 Total KATANA burned since launch
				</StyledName>
				<StyledTotalBurn>
					<BurnAmount>{totalSodaBurn ? currencyFormat(getBalanceNumber(totalSodaBurn), 3) : 'Locked'}</BurnAmount>
					{sodaBurnInUSD ? `$${currencyFormat(sodaBurnInUSD, 0)}` : ''} 
				</StyledTotalBurn>
			</StyledCard>
		</StyledWrapper>
  )
}

const StyledCard = styled.div`
  background: ${props => props.theme.bg2};
  padding: 10px 10px;
  width: 100%;
  display: flex;
  flex-direction: row;
  @media (max-width: 768px) {
	  background: ${props => props.theme.bg1};
  }
  @media (max-width: 821px) {
	  flex-direction: column;
  }
`

const StyledName = styled.div`
  font-size: 19px;
  font-weight: 500;
  margin-left: 8px;
  align-items: center;
  text-align: left;
  width: 50%;
  margin: auto;
  @media (max-width: 821px) {
	  width: 100%;
	  text-align: center;
  }
`

const StyledTotalBurn = styled.div`
  font-size: 16px;
  font-weight: 500;
  color: ${props => props.theme.red1};
  text-align: right;
  width: 50%;
  @media (max-width: 821px) {
	  width: 100%;
	  text-align: center;
	  padding-top: 15px;
  }
`

const BurnAmount = styled.div`
  color: ${props => props.theme.text1};
  font-size: 25px;
  font-weight: 600;
`

const StyledWrapper = styled.div`
  background: ${props => props.theme.bg1};
  padding: 20px 30px;
  align-items: center;
  display: column;
  width: 100%;
  @media (max-width: 768px) {
	padding: 0px;
    width: 100%;
    flex-flow: column nowrap;
    align-items: stretch;
  }
  @media (max-width: 1170px) {
    flex-flow: column nowrap;
  }
`

export default Balances
