import { useCallback, useEffect, useState } from 'react'
import BigNumber from 'bignumber.js'

import { getWethContract, getContractBalance, getBalanceNumber, getBNBPrice, getBUSDUSDTPair, getWbusdContract, getSodaAddress, getSodaPrice, getSodaContract, getMasterChefAddress, getCOLASODAPair, getColaPrice, getSushiContract} from '../sushi/utils' //getBNBBUSDPair
//import { getSodaSupply, getSodaContract } from '../sushi/utils'

// hooks
import useSushi from './useSushi'
import useBlock from './useBlock'
import { useActiveWeb3React } from '.'

const useChecking = (lpPool: string) => {
  const [totalStaked, setTotalStaked] = useState(0)
  const { account } = useActiveWeb3React()
  const sushi = useSushi()  
  const block = useBlock()
  const Contract = getWethContract(sushi)
  const wbusdContract = getWbusdContract(sushi)
  const BUSDUSDTPair = getBUSDUSDTPair(sushi)
  const sodaAddress = getSodaAddress(sushi)
  const sodaContract = getSodaContract(sushi)
  const colaMasterAddress = getMasterChefAddress(sushi)
  const COLASODAPair = getCOLASODAPair(sushi)
  const colaContract = getSushiContract(sushi)
  //const BNBBUSDpool = getBNBBUSDPair(sushi)

  const fetchBalance = useCallback(async () => {
	
	/*const bnbAmount = await getContractBalance(Contract, BNBBUSDpool)
	const busdAmount = await getContractBalance(wbusdContract, BNBBUSDpool)
	const bnbprice = getBalanceNumber(new BigNumber(busdAmount))/getBalanceNumber(new BigNumber(bnbAmount))*/
	
	const bnbprice = await getBNBPrice(sushi) //Contract, wbusdContract, BNBBUSDpool
	const sodaPrice = await getSodaPrice(sushi)
	const colaPrice = await getColaPrice(sushi)
	
	if(lpPool !== BUSDUSDTPair){
		if(lpPool === sodaAddress){
			const lpamout = await getContractBalance(sodaContract, colaMasterAddress)	//is soda only
			const totalStaked = getBalanceNumber(new BigNumber(lpamout)) * sodaPrice
			setTotalStaked(totalStaked)			
		}
		else if(lpPool === COLASODAPair){
			const sodaamout = await getContractBalance(sodaContract, lpPool)	//soda
			const colaamout = await getContractBalance(colaContract, lpPool)	//cola
			const totalStaked = (getBalanceNumber(new BigNumber(sodaamout))*sodaPrice) + (getBalanceNumber(new BigNumber(colaamout))*colaPrice)
			setTotalStaked(totalStaked)
		}
		else{ // normal
			const lpamout = await getContractBalance(Contract, lpPool)	//all
			const totalStaked = getBalanceNumber(new BigNumber(lpamout)) * bnbprice * 2
			setTotalStaked(totalStaked)
		}
	}
    else{ //stable coin
		const lpamout = await getContractBalance(wbusdContract, lpPool)	
		const totalStaked = getBalanceNumber(new BigNumber(lpamout)) * 2
		setTotalStaked(totalStaked)
	}
	
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [account, sushi])

  useEffect(() => {
    if (account && sushi) {
      fetchBalance()
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [account, setTotalStaked, block, sushi])

  return totalStaked
}

export default useChecking
